import React, { Component } from "react";
import { API } from "aws-amplify";

export default class Home extends Component {
	constructor(props) {
		super(props);
		console.log("pr", props);
	}
	componentWillReceiveProps(newProps) {
		console.log("new", newProps);
		if (newProps && newProps.authed) {
			this.getPokemon();
		}
	}
	componentDidMount() {
		if (this.props.authed) {
			this.getPokemon();
		}
	}
	async addOne() {
		const results = await API.post("wish-list-api", "want/", {
			body: 57
		});
		console.log("Results", results);
	}
	async getPokemon() {
		console.log("should get pokemon");
		const results = await API.get("wish-list-api", "query/");
		console.log("Results", results);
	}
	renderAuth() {
		return (
			<div>
				auth
				<button onClick={this.addOne}>add something</button>
			</div>
		);
	}
	render() {
		return (
			<div className="Home">
				<div className="lander">
					<h1>Pokemon Wish List</h1>
					{this.props.authed ? (
						this.renderAuth()
					) : (
						<p>Simple tracker for pokemon you want to catch</p>
					)}
				</div>
			</div>
		);
	}
}
